  outline: none;
}

.quill .ql-toolbar .ql-formats .ql-clean::before {
  content: '';
  background-color: #EF4444;
  position: absolute;
  height: 2px;
  width: 20px;
  -webkit-transform: translate(-10px, 18px);
  transform: translate(-10px, 18px);
}

.quill .ql-toolbar .ql-formats .ql-clean::after {
  background-color: var(--label-color);
  position: absolute;
  display: none;
  padding: 3px 8px;
  border-radius: 4px;
  font-size: 13px;
  line-height: 1.2;
  color: white;
  content: 'Форматуудыг цэвэрлэх';
  z-index: 10;
  -webkit-transform: translate(-12px, 30px);
  transform: translate(-12px, 30px);
  opacity: 0;
}

.quill .ql-toolbar .ql-formats .ql-clean:hover::after {
  display: inline-block;
  opacity: 1;
}

/* video */

.quill .ql-toolbar .ql-formats .ql-video {
  outline: none;
}

.quill .ql-toolbar .ql-formats .ql-video::after {
  background-color: var(--label-color);
  position: absolute;
  display: none;
  display: inline-block;
  padding: 3px 8px;
  border-radius: var(--toolbar-br);
  font-size: 13px;
  line-height: 1.2;
  color: white;
  content: 'Видео линкээр оруулах';
  z-index: 10;
  -webkit-transform: translate(-12px, 30px);
  transform: translate(-12px, 30px);
  opacity: 0;
}

.quill .ql-toolbar .ql-formats .ql-video:hover::after {
  display: inline-block;
  opacity: 1;
}

