  outline: none;
}

.quill .ql-toolbar .ql-formats .ql-image::after {
  background-color: var(--label-color);
  position: absolute;
  display: none;
  padding: 3px 8px;
  border-radius: var(--toolbar-br);
  font-size: 13px;
  line-height: 1.2;
  color: white;
  content: 'Зураг файлаар оруулах';
