  background-color:#32373d;
  padding:11.1px 3px;
  text-transform:uppercase;
  font-size:14px;
  letter-spacing:1px;
  font-weight:800;
  white-space:nowrap;
  color:white;
  text-align:center;
  border-right:1px solid rgba(255,255,255,0.2)
}

.MainParent .pro-sidebar-layout .pro-sidebar-header .headPar a{
  text-decoration:none;
  color:white
}

#root{
  height:100%
}

.app{
  height:100%;
  display:flex
}

.app .btn-toggle{
  cursor:pointer;
  width:35px;
  height:35px;
  background:#353535;
  color:#fff;
  text-align:center;
  border-radius:50%;
  display:flex;
  align-items:center;
  justify-content:center;
  font-size:15px;
  display:none
}

.app .sidebar-btn-wrapper{
  display:flex;
  align-items:center;
  justify-content:center
}

.app .sidebar-btn-wrapper .sidebar-btn{
  padding:1px 15px;
  border-radius:40px;
  background:rgba(255,255,255,0.05);
  color:#adadad;
  text-decoration:none;
  margin:0 auto;
  height:35px;
  display:flex;
  align-items:center;
  justify-content:center;
  text-overflow:ellipsis;
  overflow:hidden
}

.app .sidebar-btn-wrapper .sidebar-btn span{
  margin-left:5px;
  font-size:13px
}

.app .sidebar-btn-wrapper .sidebar-btn:hover{
  color:#d8d8d8
}

.app .collapsed .sidebar-btn{
  display:inline-block;
  border-radius:50%;
  width:35px;
  height:35px;
  line-height:40px;
  padding:0
}

.app main{
  padding:24px;
  flex-grow:1;
  display:flex;
  flex-direction:column;
  overflow-y:auto
}

.app main header{
  margin-bottom:40px;
  border-bottom:1px solid #efefef
}

.app main header h1{
  display:flex;
  align-items:center;
  -webkit-transform:translateX(-20px);
  transform:translateX(-20px)
}

.app main footer{
  margin-top:auto;
  color:#888;
  text-align:center
}

.app main footer a{
  text-decoration:none;
  color:#888
}

.app main footer a:hover{
  text-decoration:underline
}

.app .social-bagdes{
  margin-top:10px
}

.app .social-bagdes img{
  margin:5px
}

.app .block{
  display:flex;
  margin-bottom:0px;
  font-size:13px;
  margin-right:20px;
  color:#545454
}

.app .block>span{
  margin-left:10px
}

.app.rtl{
  direction:rtl;
  text-align:right
}

.app.rtl header h1{
  -webkit-transform:translateX(20px);
  transform:translateX(20px)
}

.app.rtl .block{
  direction:rtl
}

.app.rtl .block>span{
  margin-left:0;
  margin-right:10px
}

.app.rtl .sidebar-btn-wrapper .sidebar-btn span{
  margin-left:0;
  margin-right:5px
}

@media (max-width: 767.98px){
  .app{
    overflow-x:hidden
  }

  .app header h1{
    font-size:24px
  }
}

@media (max-width: 768px){
  .app .btn-toggle{
    display:flex
  }

  .app.rtl .btn-toggle{
    margin-left:auto
  }
}

.badge{
  padding:3px 6px;
  font-size:9px;
  letter-spacing:1px;
  border-radius:15px
}

.badge.red{
  color:#ffffff;
  background:#d63030
}

.badge.gray{
  color:#ffffff;
  background:#777777
}

.badge.yellow{
  color:#000000;
  background:#ffd331
}

